<template>
  <div class="carouselitem-container" ref="container" @mousemove="handleMousemove" @mouseleave="handleMouseleave">
    <div class="center" ref="center" :style="getCenterRel">
      <ImageLoader :src=carousel.bigImg :placeholder=carousel.midImg @load="this.showWords"></ImageLoader>
    </div>
    <div class="title" ref="title">{{carousel.title}}</div>
    <div class="dis" ref="dis">{{carousel.description}}</div>
  </div>
</template>

<script>
import ImageLoader from '@/components/ImageLoader'
export default {
components:{
  ImageLoader
},
data(){
  return{
    titleWidth:0,//标题宽度
    disWidth:0,//文章宽度
    containerSize:null,//容器尺寸
    centerSize:null,//图片尺寸
    mouseX:0,//鼠标相对容器坐标
    mouseY:0
  }
},
mounted() {
  this.titleWidth=this.$refs.title.clientWidth;
  this.disWidth=this.$refs.dis.clientWidth;
  this.setSize();
  window.addEventListener("resize",this.setSize);
  this.mouseX=this.startMouse.x;
  this.mouseY=this.startMouse.y;
},
destroyed() {
  window.removeEventListener("resize",this.setSize);
},
computed:{
getCenterRel(){
  if(!this.containerSize||!this.centerSize){
    return;
  }
  const surplusWidth=this.centerSize.width-this.containerSize.width;//多出的宽度
  const surplusHeight=this.centerSize.height-this.containerSize.height;
  const left= -surplusWidth/this.containerSize.width*this.mouseX;
  const top= -surplusHeight/this.containerSize.height*this.mouseY;
  return {
    transform:`translate(${left}px,${top}px)`
  }
},
startMouse(){
  return {
    x:this.containerSize.width/2,
    y:this.containerSize.height/2
  }
}
},
methods: {
  showWords(){
    this.$refs.title.style.opacity=1;
    this.$refs.title.style.width = 0;
    this.$refs.title.clientWidth;
    this.$refs.title.style.transition ="1s";
    this.$refs.title.style.width=this.titleWidth + "px";

    this.$refs.dis.style.opacity=1;
    this.$refs.dis.style.width = 0;
    this.$refs.dis.clientWidth;
    this.$refs.dis.style.transition ="2s 1s";
    this.$refs.dis.style.width=this.disWidth + "px";
  },
  setSize(){
    this.containerSize={
      width:this.$refs.container.clientWidth,
      height:this.$refs.container.clientHeight
    };
    this.centerSize={
      width:this.$refs.center.clientWidth,
      height:this.$refs.center.clientHeight
    };
  },
  handleMousemove(e){
    const rect=this.$refs.container.getBoundingClientRect();
this.mouseX=e.clientX-rect.left;
this.mouseY=e.clientY-rect.top;
  },
  handleMouseleave(){
    this.mouseX=this.startMouse.x;
    this.mouseY=this.startMouse.y;
  }
},
props:["carousel"],
}
</script>

<style scoped lang="less">
@import "~@/styles/let.less";
.carouselitem-container{
  width: 100%;
  height: 100%;
  color: #fff;
  position: relative;
overflow: hidden;

  .center{
    width: 110%;
    height: 110%;
    position: absolute;
   transition: 0.3s;
  }
  .title, 
  .dis{
    position: absolute;
    left: 30px;
    letter-spacing: 3px;
     text-shadow: 1px 0 0 rgba(0, 0, 0, .5), -1px 0 0 rgba(0, 0, 0, .5), 0 1px 0 rgba(0, 0, 0, .5), 0 -1px 0 rgba(0, 0, 0, .5);
     white-space: nowrap;
     overflow: hidden;
     opacity: 0;
  }
  .title{
    top: calc(50% - 40px);
    font-size: 2em;
  }
  .dis{
    top: calc(50% + 20px);
    font-size: 1.2em;
  }
}
</style>