<template>
  <Layout>
      <div class="main-container" v-loading="isLoading" ref="mainContainer">
      <BlogDetail :blog="data" v-if="data"></BlogDetail>
      <BlogsComment v-if="!isLoading"></BlogsComment>
      </div>
      <template #right>
          <div class="toc-container" v-loading="isLoading">
          <DetailToc :toc="data.toc" v-if="data"></DetailToc>
          </div>
      </template>
  </Layout>
</template>

<script>
import fetchData from '@/mixins/fetchData.js'
import {getBlog} from "@/api/blog.js"
import Layout from "@/components/Layout"
import BlogDetail from './BlogDetail'
import DetailToc from './DetailToc'
import BlogsComment from "./BlogsComment"
import mainScroll from "@/mixins/mainScroll.js"
export default {
mixins:[fetchData(null),mainScroll("mainContainer")],
methods: {
    async fetchData(){
        return await getBlog(this.$route.params.id)
    }
   
},

updated() {
    const hash=location.hash;
    location.hash="";
    setTimeout(()=>{
        location.hash=hash;
    },50)
},
components:{
    Layout,
    BlogDetail,
    DetailToc,
    BlogsComment,
}
}
</script>

<style scoped lang="less">
.main-container{
    overflow-y: scroll;
    height: 100%;
    box-sizing: border-box;
    padding: 20px;
    position: relative;
    width: 100%;
    overflow-x:hidden ;
     scroll-behavior: smooth;
}
.toc-container{
    height: 100%;
    width: 300px;
    box-sizing: border-box;
  padding: 20px;
  position: relative;
   overflow-y: auto;
  
}
</style>