<template>
  <ul class="listright-container">
      <li v-for="(item,i) in list" :key="i" >
          <span @click="handleStyle(item)" :class="{active:item.isSelect}">{{item.name}}</span>
          <span v-if="item.aside" class="aside" :class="{active:item.isSelect}" @click="handleStyle(item)">{{item.aside}}</span>
          <ListRight :list="item.children" @select="handleStyle"></ListRight>
      </li>
  </ul>
</template>

<script>
export default {
name:"ListRight",
props:{
    list:{
        type:Array,
        default:()=>[]
    }
},
methods: {
    handleStyle(item){
        if(!item.isSelect){
            this.$emit("select",item)
        }
    
    }
},
}
</script>

<style scoped lang="less">
@import "~@/styles/let.less";
.listright-container{
    list-style: none;
    padding: 0;
    .listright-container{
        margin-left: 1em;
    }
    li {
    min-height: 40px;
    line-height: 40px;
    cursor: pointer;
    .active {
      color: @warn;
      font-weight: bold;
    }
  }
}
.aside{
    font-size: 12px;
    margin-left: 1em;
    color: @gray;
}
</style>