export default function(mainValue) {
    return {
        mounted() {
            this.$refs[mainValue].addEventListener("scroll", this.handleSelect);
            this.$bus.$on("scrollTop", this.handleScrollTop);
        },
        beforeDestroy() {
            this.$bus.$emit("mainScroll")
            this.$refs[mainValue].removeEventListener("scroll", this.handleSelect)
            this.$bus.$off("scrollTop", this.handleScrollTop)
        },
        methods: {
            handleSelect() {
                this.$bus.$emit("mainScroll", this.$refs[mainValue])
            },
            handleScrollTop(n) {
                this.$refs[mainValue].scrollTop = n;
            }
        }
    }
}