<template>
  <img class="avatar-container" v-bind:src="url" v-bind:style="{width:size+'px',height:size+'px'}">
</template>

<script>
export default {
props:{
    url:{
        type:String,
        required:true,
    },
    size:{
        type:Number,
        default:150,
    }
},
}
</script>

<style scoped>
.avatar-container{
    border-radius: 50%;
    object-fit: cover;
    display: block;
}
</style>