<template>
  <div class="blogs-comment-container">
<MessageArea title="评论列表" :subTitle="`(${data.total})`" :list="data.rows" :isListLoading="isLoading"  @submit="handleSubmit"></MessageArea>
  </div>
</template>

<script>
import MessageArea from "@/components/MessageArea"
import fetchData from "@/mixins/fetchData.js"
import {getComments, postComment} from "@/api/blog.js"
export default {
    mixins:[fetchData({total:0,rows:[]})],
components:{
    MessageArea
},
data(){
    return{
        page:1,
        limit:10
    }
},
created() {
  this.$bus.$on("mainScroll",this.handleScroll)
},
destroyed() {
  this.$bus.$off("mainScroll",this.handleScroll)
},
computed:{
  hasMore(){
    return this.data.rows.length<this.data.total;
  }
},
methods: {
   handleScroll(dom){
     if(this.isLoading || !dom){
       return;
     }
    const range=100;
    let bs=Math.abs((dom.scrollTop+dom.clientHeight)-dom.scrollHeight);
if(bs<=range){
this.fetchMore();
}
  },
   async fetchData(){
       return await getComments(this.$route.params.id,this.page,this.limit)
    },
    async fetchMore(){
      if(!this.hasMore){
        return;
      }
      this.isLoading=true;
      this.page++;
      const rpes=await this.fetchData();
      this.data.total=rpes.total;
      this.data.rows=this.data.rows.concat(rpes.rows);
      this.isLoading=false;
    
    },
    async handleSubmit(formData, callback) {
      const resp = await postComment({
        blogId: this.$route.params.id,
        ...formData,
      });
      this.data.rows.unshift(resp);
      this.data.total++;
      callback("评论成功"); // 告诉子组件，我这边处理完了，你继续
    },

 }

}


</script>

<style scoped lang="less">
.blogs-comment-container{
    margin: 30px 0;
}
</style>