<template>
  <div class="to-top-container" v-show="show" @click="handleTop">
     Top
  </div>
</template>

<script>
export default {
data(){
    return{
        show:false
    };
},
created() {
    this.$bus.$on("mainScroll",this.handleShow)
},
destroyed() {
    this.$bus.$off("mainScroll",this.handleShow)
},
methods: {
    handleShow(dom){
        if(!dom){
            this.show=false;
            return;
        }
   if(dom.scrollTop>=500){
       this.show=true;
   }else{
       this.show=false;
   }
    },
    handleTop(){
this.$bus.$emit("scrollTop",0)
    }
},
}
</script>

<style scoped lang="less">
.to-top-container{
    position: fixed;
    bottom: 10px;
    right: 10px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    background: blue;
    line-height: 80px;
    font-size: 1.2em;
    align-items: center;
    text-align: center;
    opacity: .5;
    color: #fff;
    cursor: pointer;
}
</style>