<template>
  <div class="imageloader-container">
      <img  v-if="!everythingDone" class="placeholder" :src="placeholder" >
      <img  @load="handleload()"  :src="src" :style="{opacity:locadeOpacity ,transition:`${duration}ms`}">
  </div>
</template>

<script>
export default {
props:{
    src:{
        type:String,
        required:true
    },
    placeholder:{
        type:String,
        required:true
    },
    duration:{
        type:Number,
        default:500
    }
},
data(){
    return {
        originloade:false,//判断图片是否加载完成
        everythingDone:false//判断所有的东西是否都已经完成
    }
},
computed:{
 locadeOpacity(){
     return this.originloade?1:0;
 }
},
methods:{
    handleload(){
        this.originloade = true;
        setTimeout(() => {
             this.everythingDone = true;
            this.$emit("load");
        }, this.duration);
    }
}
}
</script>

<style scoped lang="less">
@import "~@/styles/minlx.less";
.imageloader-container{
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    img{
        .self-fill();
        object-fit: cover;
    }
    .placeholder{
        filter: blur(2vw);
        
    }
}

</style>