//content 消息内容
//type  消息类型
//container 容器
import getComponentRootDom from "./getComponentRootDom"
import Icon from "@/components/Icon/index.vue"
import styles from "./showMessage.module.less"
export default function(opations = {}) {
    const content = opations.content || "";
    const type = opations.type || "info";
    const duration = opations.duration || 2000;
    const container = opations.container || document.body;
    const div = document.createElement("div");
    const iconDom = getComponentRootDom(Icon, {
        type,
    });
    div.innerHTML = `<span>${iconDom.outerHTML}</span><div>${content}</div>`;

    div.className = `${styles.message} ${styles[`message-${type}`]}`;
    if(opations.container){
        if (getComputedStyle(container).position === "static") {
            container.style.position = "relative";
        }
    }
    container.appendChild(div);
    div.clientHeight
    div.style.opacity = 1;
    div.style.transform = "translate(-50%,-50%)";
    setTimeout(()=>{
        div.style.opacity = 0;
        div.style.transform = "translate(-50%,-50%) translateY(-15px)"
        div.addEventListener("transitionend" ,function(){
            div.remove;
            opations.callback && opations.callback();
        },{once:true})
    },duration)
}