<template>
  <div class="blog-detail-container">
    <h2>文章目录</h2>
    <ListRight :list="tocWithSelect" @select="handleSelect" />
  </div>
</template>

<script>
import ListRight from "../ListRight/index.vue"
import debounce from "@/utils/debounce.js"
export default {
components:{
  ListRight
},
props:{
  toc:{
    type:Array,
    required:true
  }
},
created() {
   this.setSelectDe=debounce(this.setSelect,50)
  this.$bus.$on("mainScroll",this.setSelectDe)
},
destroyed() {
  this.$bus.$off("mainScroll",this.setSelectDe)
},
data(){
  return{
    activeAnchor:""
  }
},
methods: {
  handleSelect(item){
  location.hash=item.anchor;
  },
  setSelect(scrollDom){
    if(!scrollDom){
      return;
    }
    this.activeAnchor="";
    const range=100;
for (const t of this.dom) {
  if(!t){
continue;
  }
  const top=t.getBoundingClientRect().top;
  if(top>=0&&top<=range){
this.activeAnchor=t.id;
return;
  }
  else if(top >range){
return;
  }
  else{
this.activeAnchor=t.id;
  }
}
  },
 
},
computed:{
  tocWithSelect(){
const getToc=(toc=[])=>{
return toc.map(t=>({
...t,
isSelect:t.anchor===this.activeAnchor,
children:getToc(t.children)
}))
}
return getToc(this.toc)
  },
   dom(){
    const doms=[];
    const addToDoms=toc=>{
    for (const t of toc) {
      doms.push(document.getElementById(t.anchor))
      if(t.children&&t.children.length>1){
        addToDoms(t.children)
      }
    }
    }
    addToDoms(this.toc);
    return doms;
  }
}
}
</script>

<style scoped lang="less">
.blog-detail-container {
  width: 100%;
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>
