<template>
  <div class="home-container" ref="container" @wheel="handlewheel" v-loading="loading" >
      <ul class="carouse-container" :style="{marginTop:marginTop}" @transitionend="handletran">
        <li v-for="item in data" :key="item.id">
          <Carouselitem :carousel="item"></Carouselitem>
        </li>
      </ul>
      <div v-show="index>=1" @click="switchTo(index -1)" class="icon icon-up">
        <Icon type="arrowUp"></Icon>
      </div>
      <div  v-show="index<data.length-1" @click="switchTo(index+1)" class="icon icon-down">
        <Icon type="arrowDown"></Icon>
      </div>
      <ul class="indicator">
        <li :class="{active:i===index}" v-for="(item,i) in data" :key="item.id" @click="switchTo(i)"></li>
      </ul>
  </div>
</template>



<style scoped lang="less">
@import "~@/styles/let.less";
@import "~@/styles/minlx.less";
.home-container{
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  cursor: pointer;
  ul{
    margin: 0;
    list-style: none;
    padding: 0;
  }
}
.icon{
  position: absolute;
  left: 50%;
  @gtp:25px;
  font-size: 30px;
  transform: translateX(-50%);
  color: #fff;
  &.icon-up{
    top: @gtp ;
    animation: Iup 2s infinite;
  }
  &.icon-down{
    top: auto;
    bottom: @gtp;
    animation: Idown 2s infinite;
  }
  @jump:5px;
  @keyframes Iup {
    0%{
      transform: translateX(-50%) translateY(@jump);
    }
    50%{
      transform: translateX(-50%) translateY(-@jump);
    }
    100%{
      transform: translateX(-50%) translateY(@jump);
    }
  }
  @keyframes Idown {
    0%{
      transform: translateX(-50%) translateY(-@jump);
    }
    50%{
      transform: translateX(-50%) translateY(@jump);
    }
    100%{
      transform: translateX(-50%) translateY(-@jump);
    }
  }
}
.carouse-container{
  width: 100%;
  height: 100%;
  transition: 500ms;
  li{
    width: 100%;
    height: 100%;
  }
}
.indicator{
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  li{
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: @words;
    cursor: pointer;
    margin-bottom: 10px;
    border: 1px solid #fff;
    box-sizing: border-box;
    &.active{
      background: #fff;
    }
  }
}

</style>

<script>
import Icon from "@/components/Icon/index.vue"
import Carouselitem from "./Carouselitem.vue"
import {mapState} from "vuex"
export default {
  components:{
    Icon,
    Carouselitem
  },
data(){
  return {
    index:0,
    containerHeight:0,
    wheelhua:false
  }
},
created() {
  this.$store.dispatch("banner/fetchBanner")
},
mounted() {
  this.containerHeight = this.$refs.container.clientHeight;
  window.addEventListener("resize",this.handleResize)
},
destroyed() {
  window.removeEventListener("resize",this.handleResize)
},
computed:{
marginTop(){
  return -this.index*this.containerHeight + "px";
  
},
...mapState("banner",["loading","data"])
},
methods: {
  switchTo(i) {
      this.index = i;
    },
    handletran(){
     this.wheelhua=false;
    },
    handlewheel(e){
      if(this.wheelhua){
        return;
      }
      if(e.deltaY<=-125&&this.index>0){
        this.wheelhua=true;
        this.index--;
      }else if(e.deltaY>=125&&this.index<this.data.length-1){
        this.wheelhua=true;
        this.index++;
      }
    },
    handleResize(){
      this.containerHeight = this.$refs.container.clientHeight;
    }

},
}
</script>